.section-five {
  @extend .section;

  .section-header {
    h1 {
      margin-bottom: 32px;
      animation: fadeInFromTop $animation-duration
        calc(#{$animation-delay} * 5.5) both;
    }

    p {
      animation: fadeInFromTop $animation-duration
        calc(#{$animation-delay} * 5.7) both;
    }
  }

  .jobs-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    box-sizing: border-box;
    animation: fadeInFromTop $animation-duration calc(#{$animation-delay} * 6)
      both;
  }

  .job-card {
    height: 15vh;
    min-height: 125px;
    border-radius: 2px;
    flex-grow: 1;
    margin: 12px;
    padding: 0 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: $animation-duration, display $animation-duration 100ms;
    border-radius: 2px;
    box-shadow: 3px 3px 5px #eee, -3px -3px 5px #fff;

    &:hover {
      background-color: $secondary;
      box-shadow: none;
      color: $secondary-text;
      cursor: pointer;

      .job-icon-container {
        .job-icon {
          color: $secondary-text;
        }
      }
    }

    .job-title {
      height: 100%;
      width: auto;
      flex-grow: 2;
      display: flex;
      font-weight: 500;
      text-align: left;
      align-items: center;
      margin: 12px;
      justify-content: flex-start;
      object-fit: contain;
    }

    .job-icon-container {
      width: 38px;
      height: 100%;
      flex-grow: 1;
      text-align: right;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      .job-icon {
        height: 38px;
        max-width: 38px;
        flex-grow: 1;
        text-align: right;
        font-size: 1.2em;
        color: $primary-text;
      }
    }
  }

  @media screen and (max-width: 768px) {
    .jobs-container {
      grid-template-columns: 1fr;
    }

    .job-card {
      width: 100%;

      .job-icon {
        font-size: 4em;
      }
    }
  }

  @media screen and (min-width: 769px) and (max-width: 992px) {
    .jobs-container {
