.section-six {
  @extend .section;
  padding: 0 18px;

  .section-header {
    h1 {
      margin-bottom: 32px;
      animation: fadeInFromTop $animation-duration
        calc(#{$animation-delay} * 6.5) both;
    }

    p {
      animation: fadeInFromTop $animation-duration
        calc(#{$animation-delay} * 6.7) both;
    }
  }

  form {
    .input-row {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      margin: 0 0 18px 0;
      animation: fadeInFromTop $animation-duration both;

      .error-message {
        color: lighten(#f00, 15);
        width: 100%;
        padding: 6px 0;
        text-align: left;
      }

      label {
        width: 100%;
        font-size: 1em;
        text-align: left;
        margin: 0 0 8px 0;
        font-weight: 200;
      }

      &:nth-child(1) {
        animation-delay: calc(#{$animation-delay} * 7);
      }

      &:nth-child(2) {
        animation-delay: calc(#{$animation-delay} * 7.1);
      }

      &:nth-child(3) {
        animation-delay: calc(#{$animation-delay} * 7.2);
      }

      &:nth-child(4) {
        animation-delay: calc(#{$animation-delay} * 7.3);
      }

      .error {
        border: 0.25em solid rgba(lighten(#f00, 35), 0.8);
        border-radius: 4px;
      }

      input {
        height: 48px;
      }

      input,
      textarea {
        width: 100%;
        font-size: 1em;
        font-weight: 400;
        outline: none;
        border: none;
        border-radius: 4px;
        padding: 8px 12px;
        background-color: transparent;
        box-shadow: 3px 3px 5px #eee, -3px -3px 5px #fff;
        -webkit-transition: all $animation-duration ease-out;
        -moz-transition: all $animation-duration ease-out;
        -ms-transition: all $animation-duration ease-out;
        -o-transition: all $animation-duration ease-out;
        transition: all $animation-duration ease-out;

        &:active,
        &:focus {
          box-shadow: inset 3px 3px 5px #eee, inset -3px -3px 5px #fff,
            3px 3px 5px #fff;
        }

        &::-webkit-input-placeholder {
          font-weight: 100;
        }

        &:-ms-input-placeholder {
          font-weight: 100;
        }

        &::placeholder {
          font-weight: 100;
        }
      }

      .disabled {
        background-color: lighten($color: $secondary, $amount: 20);
        box-shadow: none;
      }

      button {
        height: 48px;
        width: 100%;
        outline: none;
        border: none;
        border-radius: 4px;
        padding: 6px 12px;
        background-color: $secondary;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $secondary-text;
        font-size: 1.2em;
        letter-spacing: 1px;
        font-weight: 900;
        box-shadow: 3px 3px 5px grey;
        transition: $animation-duration;

        &:hover {
          box-shadow: none;
        }
      }
    }
  }
}

.footer {
  width: 100%;
  height: 68px;
  background-color: $secondary;
  display: flex;
  align-items: center;
  justify-content: center;

  span {
    margin: 0 12px;
  }
}
